    <div class="calender">
        <vx-card>
            <div class="md:flex mb-5">
                <div class="lg:w-1/6 mt-4 md:mt-0">
                    <datepicker data-vv-validate-on="blur" :placeholder="'-- Pilih Bulan --'" :minimumView="'month'" :maximumView="'month'" :format="'MMM yyyy'" v-model="date" ></datepicker>
                </div>
                <div class="lg:w-1/6 mt-4 md:mt-0 md:ml-4">
                    <!-- <vs-button color="primary" v-on:click="loadDataHistoryPesanan()">Tampilkan</vs-button> -->
                    <vs-button radius color="primary" icon="search" v-on:click="loadDataHistoryPesanan()"></vs-button>
                </div>   
            </div>
            <FullCalendar  :options="calendarOptions" ref="calendar"/>

            <vs-popup class="holamundo" title="Detail History Pesanan" :active.sync="modaldetailHistoryPesanan">
                <div class="grid grid-cols-3 gap-4 mb-4">
                    <div class="col-span-1">
                        <small class="font-bold">Nama</small>
                    </div>
                    <div class="col-span-2">
                       <h6>: {{ formdetailHistory.nama }}</h6>
                    </div>
                </div>
                <div class="grid grid-cols-3 gap-4 mb-4">
                    <div class="col-span-1">
