                    </div>
                    <div class="col-span-2">
                       <h6>: {{ formdetailHistory.tipe }}</h6>
                    </div>
                </div>
                <div class="grid grid-cols-3 gap-4 mb-4">
                    <div class="col-span-1">
                        <small class="font-bold">WhatsApp</small>
                    </div>
                    <div class="col-span-2">
                       <h6>: {{ formdetailHistory.wa }}</h6>
                    </div>
                </div>
                <div class="grid grid-cols-3 gap-4 mb-4">
                    <div class="col-span-1">
                        <small class="font-bold">Tanggal Pesan</small>
                    </div>
                    <div class="col-span-2">
                        <h6>: {{ formdetailHistory.tanggal_pesan }}</h6>
                    </div>
                </div>
                <div class="grid grid-cols-3 gap-4 mb-4">
                    <div class="col-span-1">
                        <small class="font-bold">Tanggal Ambil</small>
                    </div>
                    <div class="col-span-2">
                        <h6>: {{ formdetailHistory.tanggal_ambil }}</h6>
                    </div>
                </div>
                <div class="grid grid-cols-3 gap-4 mb-4">
                    <div class="col-span-1">
                        <small class="font-bold">Total</small>
                    </div>
                    <div class="col-span-2">
                        <h6>: Rp{{ currency(formdetailHistory.total) }}</h6>
                    </div>
                </div>
            </vs-popup>
        </vx-card>
    </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'
import FullCalendar from '@fullcalendar/vue'
import dayGridPlugin from '@fullcalendar/daygrid'
import interactionPlugin from '@fullcalendar/interaction'
import idLocale from '@fullcalendar/core/locales/id'

export default {
    data() {
        return{
            modaldetailHistoryPesanan: false,
            date: '',
            data: [
                {
                    'menu' : 'FRUIT TEA ALL VARIANT 230ML',
                    'qty' : '2',
                    'harga': 0,
                    'total' : 0
                },
                {
                    'menu' : 'PKT DADA JUMBO + NASI',
                    'qty' : '1',
                    'harga': 19000,
                    'total' : 19000
                },
                {
                    'menu' : 'PKT P ATAS JUMBO + NASI',
                    'qty' : '1',
                    'harga': 17000,
                    'total' : 17000
                },
                {
                    'menu' : 'SAMBAL GEPREK MB 20 GR',
                    'qty' : '1',
                    'harga': 3500,
                    'total' : 3500  
                },

            ],
            history: [] ,
            calendarOptions: {
                plugins: [ dayGridPlugin, interactionPlugin ],
                initialView: 'dayGridMonth',
                locale: idLocale,
                // showNonCurrentDates: false,
                dateClick: this.handleDateClick,
                eventClick: this.handleEventClick,
                initialDate: new Date(),
                events: []
            },
            formdetailHistory: {
                nama: '',
                tipe: '',
                wa: '',
                tanggal_pesan: '',
                tanggal_ambil: '',
                total: ''
            }
        }
    },

    components: {
        Datepicker,
        FullCalendar
    },

    mounted() {
        this.date = this.dateNow();
    },

    methods: {
        handleDateClick: function(arg) {
            alert('date click! ' + arg.dateStr)
        },

        dateNow() {
            const now = new Date();
            const year = now.getFullYear();
            const month = String(now.getMonth() + 1).padStart(2, '0');
            const day = String(now.getDate()).padStart(2, '0');
            const hours = String(now.getHours()).padStart(2, '0');
            const minutes = String(now.getMinutes()).padStart(2, '0');
            const seconds = String(now.getSeconds()).padStart(2, '0');

            return `${day}-${month}-${year} ${hours}:${minutes}:${seconds}`;
        },

        loadDataHistoryPesanan() {
            const now = new Date(this.date);
            const year = now.getFullYear();
            const month = String(now.getMonth() + 1).padStart(2, '0');
            const day = String(now.getDate()).padStart(2, '0');

            this.$vs.loading();
            this.$http.get('pos/pesanan/getData/' + `${year}-${month}`)
            .then(response => {
                this.history = response.data.message;
                this.$refs.calendar.getApi().gotoDate(`${year}-${month}-${day}`);
                this.calendarOptions.events = this.transformDataForCalendar(this.history);
            })
            .catch(err=>{
                this.$vs.loading.close();
                this.$swal.fire(
                    'Gagal !',
                    err.response.data.message,
                    'error'
                )
            })
            .finally(() => {this.$vs.loading.close()});
        },

        transformDataForCalendar(data) {
            return data.map(event => ({
                nama: event.nama,
                wa: event.wa,
                tipe: event.tipe,
                tanggal_pesan: event.tanggal_pesan,
                tanggal_ambil: event.tanggal_ambil,
                total: event.total,
                title: `${event.nama} - ${event.tipe}`,
                start: event.tanggal_pesan,
                end: this.formatEndDate(event.tanggal_ambil),
            }))
        },

        formaDate(dateString) {
            const now = new Date(dateString);
            const year = now.getFullYear();
            const month = String(now.getMonth() + 1).padStart(2, '0');
            const day = String(now.getDate()).padStart(2, '0');
            return `${year}-${month}-${day}`;
        },

        formatEndDate(dateString) {
            const now = new Date(dateString);
            const year = now.getFullYear();
            const month = String(now.getMonth() + 1).padStart(2, '0');
            const day = String(now.getDate()+1).padStart(2, '0');
            return `${year}-${month}-${day}`;
        },

        handleEventClick(info) {
            this.modaldetailHistoryPesanan = true;
            this.formdetailHistory.nama =  info.event.extendedProps.nama;
            this.formdetailHistory.tipe =  info.event.extendedProps.tipe;
            this.formdetailHistory.wa =  info.event.extendedProps.wa;
            this.formdetailHistory.tanggal_pesan =  info.event.extendedProps.tanggal_pesan;
            this.formdetailHistory.tanggal_ambil =  info.event.extendedProps.tanggal_ambil;
            this.formdetailHistory.total =  info.event.extendedProps.total;
        },

        currency(value) {
            return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
        },
    }
}
</script>

